import toArray from './toArray.js';

// global songs
const player = document.getElementById('player');
let current = 0;
setSrc();
player.addEventListener('ended', () => {
  if (current < songs.length - 1) current++;
  else current = 0;
  setSrc();
});

// Song selector:
toArray(document.querySelectorAll('[data-video]')).forEach(item => {
  item.addEventListener('click', e => {
    current = songs.indexOf(songs.find(i => i.video === e.target.dataset.video));
    setSrc();
  });
});

function setSrc() {
  player.src = 'videos/' + songs[current].video;
}
